.chatroom-panel {
  min-width: 60%;
  resize: both; }
  .chatroom-panel .chatroom-body {
    height: calc(100% - 8rem); }
    .chatroom-panel .chatroom-body .chat-content {
      border: 1px solid #ccc;
      width: 100%;
      height: 100%;
      border-left: none; }
  .chatroom-panel .message-input-container {
    height: 8rem;
    border-top: 1px solid lightgray;
    background-color: lightgray; }
    .chatroom-panel .message-input-container .message-input.form-upper, .chatroom-panel .message-input-container .message-input.form-lower {
      height: 50%;
      background-color: white;
      width: 100%;
      z-index: 1; }
    .chatroom-panel .message-input-container .message-input.form-lower {
      padding: 0 1rem; }
      .chatroom-panel .message-input-container .message-input.form-lower .submit-message-input {
        width: calc(100% - 2.5rem);
        height: 80%;
        border: none;
        resize: none; }
    .chatroom-panel .message-input-container .message-input .submit-message-btn {
      position: relative;
      width: 2rem;
      height: 2rem;
      cursor: pointer;
      color: transparent;
      border: none; }
      .chatroom-panel .message-input-container .message-input .submit-message-btn::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-blend-mode: color;
        background-size: cover;
        background-image: url("../../image/send-btn.svg"); }
      .chatroom-panel .message-input-container .message-input .submit-message-btn:hover {
        background-color: rgba(255, 255, 255, 0.5); }
    .chatroom-panel .message-input-container .message-input .media-btn {
      color: #2e555f;
      height: 3.5rem;
      width: 3.5rem;
      font-size: 1.5rem;
      border: none;
      border-radius: 50%;
      background-color: transparent; }
      .chatroom-panel .message-input-container .message-input .media-btn:hover, .chatroom-panel .message-input-container .message-input .media-btn[active~='true'] {
        background-color: #ccc; }



