.panel-default {
  border-color: #ddd; }

.panel {
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05); }

.panel-default > .panel-heading {
  color: #333;
  background-color: #f5f5f5;
  border-color: #ddd; }

.panel-heading {
  padding: 10px 15px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px; }

.panel-body {
  padding: 15px; }

.add-msg-btn-padding {
  padding: 5%; }

.error-msg, .error-input {
  color: red; }

.add-text-btn-style {
  width: 20%;
  margin: 0.5%;
  padding: 0.5%;
  float: left; }

.remove-btn {
  float: right;
  cursor: pointer;
  font-size: 11px;
  color: red; }

.composes h4 {
  padding: .75rem; }

.Compose {
  display: block;
  padding-bottom: 5%; }
  .Compose thead, .Compose tbody {
    display: block; }
    .Compose thead tr, .Compose tbody tr {
      display: -ms-flexbox;
      display: flex;
      letter-spacing: .1rem;
      color: #333; }
      .Compose thead tr .text, .Compose tbody tr .text {
        width: 50%;
        word-wrap: break-word; }
      .Compose thead tr .time, .Compose tbody tr .time {
        width: 20%;
        word-wrap: break-word; }
      .Compose thead tr .cata, .Compose tbody tr .cata {
        width: 15%; }
      .Compose thead tr .edit, .Compose tbody tr .edit {
        width: 15%; }
        .Compose thead tr .edit button, .Compose tbody tr .edit button {
          display: block;
          font-size: 1rem;
          letter-spacing: unset; }
        .Compose thead tr .edit .btn-danger, .Compose tbody tr .edit .btn-danger {
          margin-top: 10%; }

.compose-modal .compose-textarea {
  height: 5rem;
  resize: none; }

@media (min-width: 768px) {
  .Compose thead tr .time, .Compose tbody tr .time {
    width: 15%; }
  .Compose thead tr .edit, .Compose tbody tr .edit {
    width: 20%; }
    .Compose thead tr .edit button, .Compose tbody tr .edit button {
      display: inline; }
    .Compose thead tr .edit .btn-danger, .Compose tbody tr .edit .btn-danger {
      margin-top: 0%;
      margin-left: 5%; } }

@media (min-width: 1024px) {
  .composes-wrapper {
    padding: 4%; }
  .Compose thead tr .text, .Compose tbody tr .text {
    width: 60%; }
  .Compose thead tr .time, .Compose tbody tr .time {
    width: 10%; }
  .Compose thead tr .edit, .Compose tbody tr .edit {
    width: 15%; } }



