.ticket-panel .ticket-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  border: 1px solid #055257;
  border-radius: .25rem;
  font-size: .75rem; }

.ticket-panel table {
  table-layout: fixed;
  width: 100%; }

.ticket-panel thead {
  border-bottom: 1px solid #055257;
  border-radius: .25rem; }

.ticket-panel td,
.ticket-panel th {
  text-align: left;
  padding: .25rem;
  margin: 2px 0;
  max-height: 24px; }

.ticket-panel tr:nth-child(even) {
  background-color: #33ccff;
  color: white; }

.ticket-panel th.chsr .ticket-add {
  clear: both;
  float: right;
  cursor: pointer;
  color: #33ccff; }

.ticket-panel .ticket-search-bar {
  border-radius: 5px; }

.ticket-panel .ticket-description {
  max-width: 50px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap; }

.ticket-panel .ticket-row {
  cursor: pointer; }

.due-time-text .overdue {
  padding: 1px 10px;
  margin: 1px;
  border-radius: 8px;
  background-color: #e34f4f;
  color: white;
  font-weight: bold; }

.due-time-text .non {
  background-color: #5bc35c; }



