.chat-wrapper {
  font-family: Microsoft JhengHei;
  height: calc(100% - 50px);
  overflow-y: hidden;
  background-color: #f7f8f9; }
  .chat-wrapper .chatroom-container {
    overflow: hidden; }
    .chat-wrapper .chatroom-container.open::before,
    .chat-wrapper .chatroom-container.open .watermark-text {
      display: none; }
    .chat-wrapper .chatroom-container::before {
      content: "";
      position: absolute;
      left: calc(50% - 8rem);
      top: 0;
      width: 16rem;
      height: 100%;
      opacity: .5;
      background-color: transparent;
      background-image: url("../../image/logo.png");
      background-position: center calc(50% - 4rem);
      background-size: contain;
      background-repeat: no-repeat; }
    .chat-wrapper .chatroom-container .watermark-text {
      top: calc(50% + 4rem);
      width: 100%;
      color: #8e8e8e;
      opacity: .5;
      text-indent: 1rem;
      will-change: transform;
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0); }

.profile-panel {
  border-radius: .25rem;
  background-color: #f7f8f9; }



