.keywordreplies h4 {
  padding: .75rem; }

.KeywordreplyTable {
  display: block;
  padding-bottom: 5%; }
  .KeywordreplyTable thead, .KeywordreplyTable tbody {
    display: block; }
    .KeywordreplyTable thead tr, .KeywordreplyTable tbody tr {
      display: -ms-flexbox;
      display: flex;
      letter-spacing: .1rem;
      color: #333; }
      .KeywordreplyTable thead tr .keywords, .KeywordreplyTable tbody tr .keywords {
        width: 30%;
        word-wrap: break-word; }
      .KeywordreplyTable thead tr .text, .KeywordreplyTable tbody tr .text {
        width: 50%;
        word-wrap: break-word; }
      .KeywordreplyTable thead tr .count, .KeywordreplyTable tbody tr .count {
        width: 10%;
        display: none; }
      .KeywordreplyTable thead tr .edit, .KeywordreplyTable tbody tr .edit {
        width: 20%; }
        .KeywordreplyTable thead tr .edit button, .KeywordreplyTable tbody tr .edit button {
          display: block;
          font-size: 1rem;
          letter-spacing: unset; }
        .KeywordreplyTable thead tr .edit .btn-danger, .KeywordreplyTable tbody tr .edit .btn-danger {
          margin-top: 10%; }

@media (min-width: 480px) {
  .KeywordreplyTable thead tr .keywords, .KeywordreplyTable tbody tr .keywords {
    width: 25%; }
  .KeywordreplyTable thead tr .count, .KeywordreplyTable tbody tr .count {
    width: 12.5%;
    display: block; }
  .KeywordreplyTable thead tr .edit, .KeywordreplyTable tbody tr .edit {
    width: 12.5%; } }

@media (min-width: 1024px) {
  .keywordreplies-wrapper {
    padding: 4%; }
  .KeywordreplyTable thead tr .keywords, .KeywordreplyTable tbody tr .keywords {
    width: 20%; }
  .KeywordreplyTable thead tr .text, .KeywordreplyTable tbody tr .text {
    width: 55%; }
  .KeywordreplyTable thead tr .count, .KeywordreplyTable tbody tr .count {
    width: 10%; }
  .KeywordreplyTable thead tr .edit, .KeywordreplyTable tbody tr .edit {
    width: 15%; }
    .KeywordreplyTable thead tr .edit button, .KeywordreplyTable tbody tr .edit button {
      display: inline; }
    .KeywordreplyTable thead tr .edit .btn-danger, .KeywordreplyTable tbody tr .edit .btn-danger {
      margin-top: 0%;
      margin-left: 2%; } }



