.reset-container {
  height: 100%;
  padding-top: 4%; }
  .reset-container::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(40%, #33ccff), color-stop(60%, #fff));
    background: -webkit-linear-gradient(#33ccff 40%, #fff 60%);
    background: -o-linear-gradient(#33ccff 40%, #fff 60%);
    background: linear-gradient(#33ccff 40%, #fff 60%); }
  .reset-container .chsr.input-group-prepend {
    width: 3rem; }
  .reset-container .padding-left-right {
    padding: 0 2.5rem; }
  .reset-container .logo-container {
    margin-bottom: 0; }
  .reset-container .chatshier-logo img {
    width: 4rem;
    height: 4rem; }
  .reset-container .form-container {
    max-width: 40rem;
    margin: 1rem 0 2rem;
    border: 1px solid #e5e5e5;
    background-color: #fff;
    border-radius: 1rem;
    -webkit-box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }
  .reset-container .reset-title {
    margin: 3rem 0; }
  .reset-container .reset-trouble {
    margin: 1.5rem 0; }
    .reset-container .reset-trouble .link-text {
      color: #33ccff;
      cursor: pointer; }
  @media only screen and (max-width: 480px) {
    .reset-container {
      padding: 2rem 1rem 0; }
    .reset-container .padding-left-right {
      padding: 0 1rem; }
    .reset-container .reset-logo {
      margin-bottom: 1.5rem; } }
  @media only screen and (min-width: 480px) and (max-width: 768px) {
    .reset-container {
      padding: 5rem 3rem 0; }
    .reset-container .padding-left-right {
      padding: 0 2rem; }
    .reset-container .reset-logo {
      margin-bottom: 2.5rem; } }



