.sign-logo-container {
  margin-bottom: 0; }
  .sign-logo-container .chatshier-logo img {
    width: 4rem;
    height: 4rem; }
  @media only screen and (max-width: 480px) {
    .sign-logo-container {
      margin-bottom: 1.5rem; } }
  @media only screen and (min-width: 480px) and (max-width: 768px) {
    .sign-logo-container {
      margin-bottom: 2.5rem; } }

.form-container {
  max-width: 40rem;
  margin: 1rem 0 2rem;
  border: 1px solid #e5e5e5;
  background-color: #fff;
  border-radius: .5rem;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19); }
  .form-container .sign-title {
    margin: 3rem 0 1.5rem; }
  .form-container .sign-form {
    padding: 0 .25rem; }
    .form-container .sign-form .chsr.input-group-prepend {
      width: 3rem; }
    .form-container .sign-form .link-text {
      color: #33ccff;
      cursor: pointer; }
    @media only screen and (max-width: 480px) {
      .form-container .sign-form .form-padding {
        padding: 0 .5rem; } }
    @media only screen and (min-width: 480px) and (max-width: 768px) {
      .form-container .sign-form .form-padding {
        padding: 0 2rem; } }



