.page-wrapper {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  position: relative;
  max-width: calc(100% - 16rem);
  -webkit-transition: max-width, opacity 300ms;
  -o-transition: max-width, opacity 300ms;
  transition: max-width, opacity 300ms; }
  .page-wrapper.put-away {
    max-width: calc(100% - 4rem); }
  @media (max-width: 576px) {
    .page-wrapper {
      max-width: 100%; } }



