.signin-container {
  height: 100%;
  padding-top: 4%; }
  .signin-container::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(40%, #33ccff), color-stop(70%, #fff));
    background: -webkit-linear-gradient(#33ccff 40%, #fff 70%);
    background: -o-linear-gradient(#33ccff 40%, #fff 70%);
    background: linear-gradient(#33ccff 40%, #fff 70%); }
  @media only screen and (max-width: 480px) {
    .signin-container {
      padding: 2rem .5rem 0; } }
  @media only screen and (min-width: 480px) and (max-width: 768px) {
    .signin-container {
      padding: 5rem 3rem 0; } }



