.user-modal {
  width: 100% !important;
  max-width: 100% !important;
  height: 100%;
  margin: 0 !important; }
  .user-modal .modal-content {
    height: 100%; }
  .user-modal .modal-header {
    background-color: #4a8998; }
    .user-modal .modal-header .close {
      color: white;
      text-shadow: 0 1px 0 white;
      opacity: 1; }
  .user-modal .modal-body {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch; }



